.App {
  text-align: center;
  letter-spacing: normal !important;
  font-family: Circular, -apple-system, BlinkMacSystemFont, Roboto,
    Helvetica Neue, sans-serif !important;
}

.App-header {
  background-image: url("https://a0.muscache.com/4ea/air/v2/pictures/a76de86e-a94a-4863-adb7-e8a86be06290.jpg?t=r:w1550-h1037-sfit,e:fjpg-c80");
  min-height: 130vh;
  background-size: cover;
  display: stretch;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  background-position: center;
  margin-top: -40px;
}

.BelowForm {
  min-width: 400px;
  height: 150px;
  margin-top: -25em;
  margin-left: 40px;
  background-color: #fff;
  display: block;
  position: absolute;
  text-align: left;
  border-radius: 5px;
  letter-spacing: normal !important;
  font-family: Circular;
}

.BelowForm h6,
a {
  margin-left: 1em;
}
.Cancel {
  border: 1px solid #696969;
  display: inline-block;
  width: 1150px;
  border-radius: 5px;
}

.CancelText {
  text-align: left;
  font-size: 16px;
  margin-left: 5px;
}

.CancelVertical {
  border-left: 7px solid orange;
  border-radius: 4px 0px 0px 4px;
  height: 90px;
}

.Covid {
  text-align: left;
  margin-left: 3em;
  position: static;
  display: block;
}

.donateLink {
  font-weight: bold;
  color: black;
  margin-left: 3px;
}

.Exp {
  border: 1px solid #696969;
  border-radius: 5px;

  margin-left: 10px;
  margin-right: 10px;
}
#expRow {
  justify-content: center;
}
/* ^^^ Centers entire row */
.formWrapper {
  min-width: 400px;
  height: 350px;
  margin-top: -50em;
  margin-left: 40px;
  background-color: #fff;
  display: block;
  position: absolute;
  border-radius: 5px;
  font-family: Circular;
}

.formWrapper label {
  text-align: left;
}

.formWrapper label {
  font-family: Circular;
  font-size: 15px;
}
.footerHr {
  display: inline-block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;

  padding: 0;
  width: 75%;
  margin-left: 0;
}

.firsthr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}
.hrBelow {
  border: 7px solid orange;
  margin-top: -5px;
  border-radius: 5px 5px 0px 0px;
}

.icon {
  margin-left: -358px;
  margin-top: 3px;
}
