.innerText {
  text-align: left;
  display: block;
  margin-top: -115px;
  width: 600px;
  margin-left: 600px;
}

.navbar {
  margin-top: -20px;
}

.nav-item {
  font-size: 19px;
  font-family: charter;
  color: black;
  font-weight: bold;
}

#submit {
  background-color: tomato;
  float: right;
  border: none;
  margin-right: 5px;
}
* {
  box-sizing: border-box;
}
